<template>
    <div class="tank" :class="{ enemy: enemy }" :style="{
        backgroundColor: color,
        top: top + '%',
        left: left + '%',
        transform: `rotate(${-angle}deg)`
    }">
        <span class="tank-cannon" :style="{ backgroundColor: color }"></span>
    </div>
</template>

<script>
export default {
    props: {
        color: {
            default: "#FF4C4B",
            type: String,
        },
        enemy: {
            default: false,
            type: Boolean,
        },
        top: {
            default: 0,
            type: Number,
        },
        left: {
            default: 0,
            type: Number,
        },
        angle: {
            default: 0,
            type: Number,
        },
    }
}
</script>

<style>
    .tank {
        width: 20px;
        height: 20px;
        position: absolute;
    }

    .tank .tank-cannon {
        content: "";
        width: 4px;
        height: 8px;
        position: absolute;
        top: -8px;
        left: 8px;
    }

    .tank.enemy .tank-cannon {
        content: "";
        width: 4px;
        height: 8px;
        position: absolute;
        bottom: -8px;
        top: unset;
        left: 8px;
    }
</style>