<template>
  <div id="home">
    <nav class="nav-bar">
      <button @click="is_paused ? play_game() : pause_game()" class="btn btn-pause">
        <svg v-if="is_paused" width="2em" height="2em" viewBox="0 0 448 512"><path fill="currentColor" d="M424.4 214.7L72.4 6.6C43.8-10.3 0 6.1 0 47.9V464c0 37.5 40.7 60.1 72.4 41.3l352-208c31.4-18.5 31.5-64.1 0-82.6z"></path></svg>
        <svg v-else width="2em" height="2em" viewBox="0 0 448 512"><path fill="currentColor" d="M144 479H48c-26.5 0-48-21.5-48-48V79c0-26.5 21.5-48 48-48h96c26.5 0 48 21.5 48 48v352c0 26.5-21.5 48-48 48zm304-48V79c0-26.5-21.5-48-48-48h-96c-26.5 0-48 21.5-48 48v352c0 26.5 21.5 48 48 48h96c26.5 0 48-21.5 48-48z"></path></svg>
      </button>

      <span class="point" v-text="point"></span>
    </nav>

    <transition name="fade">
      <div v-if="is_game_over" class="message-container">
        <div>
          <p class="message">Game Over ðŸ˜¢</p>

          <button @click="play_again">Play Again</button>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <div v-if="is_paused && !is_game_over" class="message-container">
        <div>
          <p class="message" v-if="is_started">Game is Paused</p>
          <p class="message" else>Start New Game</p>

          <template v-if="is_started">
            <button @click="play_game">Resume</button>

            <button @click="play_again" style="margin-left: 1em">Play Again</button>
          </template>
          
          <template v-else>
            <input v-model="player_name" type="text" placeholder="Please enter your name:" />
            <br/>
            <br/>
            <button  @click="play_game">Start new game</button>

            <br/>
            <br/>
            <hr/>
            <br/>

            <table width="100%" border="1">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Point</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(item, index) of ranks" :key="index">
                  <td v-text="item.name"></td>
                  <td v-text="item.point"></td>
                </tr>
              </tbody>
            </table>
          </template>
        </div>
      </div>
    </transition>

    <transition-group name="fade" tag="span">
      <tank
        v-for="enemy in enemies"
        :key="enemy.id"
        enemy
        :top="enemy.top"
        :left="enemy.left"
        :angle="enemy.angle"
      />
    </transition-group>

    <transition-group name="fade" tag="span">
      <span
        class="bullet"
        v-for="(item, index) in bullets"
        :key="index"
        :style="{ top: item.top + '%', left: item.left + '%', backgroundColor: item.color }"
      ></span>
    </transition-group>

    <transition-group name="fade" tag="span">
      <span
        class="gift"
        v-for="(gift, index) in gifts"
        :key="index"
        :class="`gift-${gift.type}`"
        :style="{ top: gift.top + '%', left: gift.left + '%' }"
      ></span>
    </transition-group>

    <tank :color="current_type_color" class="player" :class="{ 'green-shadow': current_type == 'green' }" :top="90" :left="player_position" />
  </div>
</template>

<script>
// @ is an alias to /src
import Tank from '@/components/Tank.vue'

export default {
  name: 'Home',
  components: {
    Tank
  },

  data() {
    return {
      is_paused: true,
      is_game_over: false,
      is_started: false,

      intervals: {
        move: null,
        add: null,
        bullets: null,
        add_gift: null,
        move_gift: null,
      },

      last_id: 3,

      enemies: [],
      bullets: [],
      gifts: [],

      current_type: null,

      point: 0,
      player_position: 48,

      player_name: "",

      ranks: [],
    }
  },

  mounted() {
    try {
      this.ranks = JSON.parse(localStorage.getItem("ranks")) ? JSON.parse(localStorage.getItem("ranks")) : [];
    } catch (error) {
      this.ranks = [];
    }
    
    document.addEventListener("keydown", this.handle_keyboard_events);
  },

  methods: {
    handle_keyboard_events(e) {
      if (e.keyCode == 37) {
        this.player_position -= 1;
      }
      
      if ( e.keyCode == 39) {
        this.player_position += 1;
      }
      
      if ( e.keyCode == 32) {
        if (this.current_type == "gold")
        {
          this.fire_bullet();
          setTimeout(this.fire_bullet, 100);
          setTimeout(this.fire_bullet, 200);
          setTimeout(this.fire_bullet, 300);
          setTimeout(this.fire_bullet, 400);
        }
        else if (this.current_type == "violet")
        {
          this.fire_bullet();
          setTimeout(this.fire_bullet, 100);
          setTimeout(this.fire_bullet, 200);
        } else {
          this.fire_bullet();
        }
      }
    },

    fire_bullet() {
      this.bullets.push({
        top: 90,
        left: this.player_position + 0.6,
        color: this.current_type_color
      });
    },

    move_tanks() {
      this.intervals.move = setInterval(() => {
        this.enemies.forEach(enemy => {
          if ( enemy.left < 3 || enemy.left >= 95 || enemy.top >= 90 )
            this.enemies.splice(this.enemies.indexOf(enemy), 1);

          if (this.current_type != 'green' && enemy.top >= 90) return this.game_over();
          
          enemy.left += Math.sin(enemy.angle * Math.PI / 180) * 0.1 * enemy.speed;
          enemy.top += Math.cos(enemy.angle * Math.PI / 180) * 0.1 * enemy.speed;
        });
      }, 10);
    },

    move_bullets() {
      this.intervals.bullets = setInterval(() => {
        this.bullets.forEach(item => {
          if ( item.top <= 0 )
            this.bullets.splice(this.bullets.indexOf(item), 1);

          this.enemies.forEach(enemy => {
            let offset_top = Math.abs( Math.round(enemy.top) - Math.round(item.top) );
            let offset_left = Math.abs( Math.round(enemy.left) - Math.round(item.left) );

            if ( offset_top <= 2 && offset_left <= 2 ) {
              this.point += 10;
              this.enemies.splice(this.enemies.indexOf(enemy), 1);
              this.bullets.splice(this.bullets.indexOf(item), 1);
            }
          });

          item.top -= 0.1;
        });
      }, 2);
    },

    move_gifts() {
      this.intervals.move_gift = setInterval(() => {
        this.gifts.forEach(gift => {
          if ( gift.left >= 95 || gift.top >= 95 )
            this.gifts.splice(this.gifts.indexOf(gift), 1);

          let offset_top = Math.abs( 90 - Math.round(gift.top) );
          let offset_left = Math.abs( Math.round(this.player_position) - Math.round(gift.left) );

          if ( offset_top <= 2 && offset_left <= 2 ) {
            this.current_type = gift.type;
            this.gifts.splice(this.gifts.indexOf(gift), 1);
            setTimeout(() => this.current_type = null, gift.type == 'violet' ? 10000 : 5000);
          }

          gift.top += 0.1 * gift.speed;
        });
      }, 10);
    },

    add_tanks() {
      let max_position = 80;
      let min_position = 20;

      let max_angle = 45;
      let min_angle = -45;

      let max_speed = 0.2;
      let min_speed = 1.2;

      this.intervals.add = setInterval(() => {
        this.enemies.push({
          id: ++this.last_id,
          top: 0,
          left: Math.random() * (max_position - min_position) + min_position,
          angle: Math.random() * (max_angle - min_angle) + min_angle,
          speed: Math.random() * (max_speed - min_speed) + min_speed
        });
      }, 1600);
    },

    add_gifts() {
      let max_position = 90;
      let min_position = 10;

      let max_speed = 0.2;
      let min_speed = 1.2;

      this.intervals.add_gift = setInterval(() => {
        this.gifts.push({
          top: 0,
          left: Math.random() * (max_position - min_position) + min_position,
          speed: Math.random() * (max_speed - min_speed) + min_speed,
          type: ['violet', 'gold', 'green'][ Math.round(Math.random() * 2) ]
        });
      }, 5000);
    },

    pause_game() {
      this.is_paused = true;
      clearInterval(this.intervals.move);
      clearInterval(this.intervals.add);
      clearInterval(this.intervals.bullets);
      clearInterval(this.intervals.move_gift);
      clearInterval(this.intervals.add_gift);
    },

    play_game() {
      if ( !this.player_name )
        return alert("Please Enter Your name")

      this.is_started = true;
      this.is_paused = false;
      this.move_tanks();
      this.add_tanks();
      this.add_gifts();
      this.move_bullets();
      this.move_gifts();
    },

    game_over() {
      this.pause_game();
      this.is_game_over = true;
      this.set_new_rank();
    },

    set_new_rank() {
      let ranks = [];

      try {
        ranks = JSON.parse(localStorage.getItem("ranks"));
        if (!ranks) ranks = [];
      } catch (error) {
        ranks = [];
      }

      ranks.push({
        name: this.player_name,
        point: this.point
      });

      this.ranks = ranks;
      localStorage.setItem('ranks', JSON.stringify(ranks));
    },

    play_again() {
      this.pause_game();
      this.enemies = [];
      this.gifts = [];
      this.point = 0;
      this.is_game_over = false;
      this.play_game();
    },
  },

  computed: {
    current_type_color() {
      if (this.current_type == 'violet')
        return "#5D44FE";

      else if (this.current_type == 'gold')
        return "#FFC638";

      else if (this.current_type == 'green')
        return "#61FFBC";

      return "#fff";
    },
  }
}
</script>

